* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #333;
  width: 100%;
  background-color: #fff;
  font-family: Poppins, sans-serif;
}

.wrapper {
  width: 92%;
  margin: auto;
}

ul {
  list-style: none;
}

section {
  flex-direction: column;
  align-items: center;
  margin: 0 0 100px;
  display: flex;
}

img {
  max-width: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  border-radius: 24px;
}

input, button {
  border: none;
  border-radius: 12px;
  font-family: Poppins, sans-serif;
  font-size: 14px;
  line-height: 22px;
  transition: all .3s ease-in-out;
}

label, input, button {
  cursor: pointer;
  font-weight: 500;
}

input, button, a {
  outline-offset: 3px;
  outline: 3px solid #0000;
}

input:focus-visible, button:focus-visible, a:focus-visible {
  outline: 3px solid #2d9cdb;
}

a {
  color: #333;
  border-radius: 8px;
  text-decoration: none;
  transition: all .3s ease-in-out;
}

a:focus-visible {
  border-radius: 4px;
}

button {
  text-align: center;
  padding: 10px 13px;
}

form {
  flex-direction: column;
  margin: 10px 0;
  display: flex;
  position: relative;
}

form label {
  color: #828282;
  margin: 0 0 5px;
  font-size: 10px;
  line-height: 1.6;
}

input {
  color: #bdbdbd;
  height: 50px;
  width: 100%;
  background-color: #f2f2f2;
  padding: 15px 100px 6px 19px;
  font-family: Poppins, sans-serif;
}

input:hover, input:focus {
  color: #333;
  background-color: #fff;
  outline: 3px solid #2d9cdb;
}

form button {
  color: #fff;
  height: 39px;
  letter-spacing: .9px;
  width: 71px;
  background-color: #2d9cdb;
  position: absolute;
  bottom: 4px;
  right: 4px;
}

form button:hover, form button:focus {
  background-color: #27ae60;
}

ul li a {
  transition: all .3s ease-in-out;
}

ul li a:focus, ul li a:hover {
  text-decoration: underline;
}

